import React, { Fragment } from 'react';
import { Link } from 'gatsby';

function FooterLeft() {
	return (
		<Fragment>
			<p style={{ color: '#9e9e9e' }} className="text-right m-0 mt-2 mb-2">
				  
			</p>
			<hr className="m-0" />
			<div className="d-flex justify-content-between">
				<div>
					<Link to="/blog/game" className="text-right d-block mt-2">
						 
					</Link>
					<Link to="/blog/tech" className="text-right d-block mt-2">
						
					</Link>
					<Link to="/blog/ps4" className="text-right d-block mt-2">
						 
					</Link>
					<Link to="/blog/pc" className="text-right d-block mt-2">
						  
					</Link>
				</div>
				<div>
					<Link to="/blog/xbox" className="text-right d-block mt-2">
						 
					</Link>
					<Link to="/blog/mobile" className="text-right d-block mt-2">
						 
					</Link>
					<Link to="/blog/other" className="text-right d-block mt-2">
						
					</Link>
					<Link to="/blog" className="text-right d-block mt-2">
						  
					</Link>
				</div>
			</div>
		</Fragment>
	);
}

export default FooterLeft;
